* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Open Sans;
    color: #4A4A4A;
}

h1 {
    color: #4A4A4A;
}

.link-github:hover {
    text-decoration: underline;
}

.main {
    display: flex;
    margin-left: auto;
    margin-right: auto;
}

.technical-task {
    flex: 1 0 auto;
    max-width: 450px;
    padding-right: 20px;
    padding-left: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
    border: 1px solid #dedcdc;
    border-radius: 5px;
}

.app-container {
    display: flex;
    flex-direction: column;
    text-align: center;
}

.app-header {
    align-self: center;
    margin-top: 50px;
}

